.mg-auto{
    margin:0 auto;
}
.center{
    text-align:center;
}
.trans{
    background:transparent !important;
}
.introduction {
    widows: 100%;
    height:400px;
    background:#17529E url(https://wallpapercave.com/wp/wp2337006.jpg) no-repeat center fixed;
    padding-top:100px;
    background-size:cover;
    color:white;
}
.introduction h1{
    color:yellow;
}
.introduction h3{
    color:white;
}
.candidate{
    margin-top:20px;
}
.candidate img {
    display:inline-block;
    margin-top:40px;
    height:200px;
}
.candidate button {
    width:100px;
}
.result{
    height:300px;
    /* background:rgb(145, 193, 230) */
}
.big-number{
    margin:0 5.5em;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.left-text{
    color:#ff5252
}
.right-text{
    color:#2196f3;
}

