.name{
    font-size: 18px;
    font-weight:700;
}
.vote{
    font-size: 30px;
    font-weight:800;
}
.candidate-icon{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    height: 70px;
    margin-top:-50px;
}
.candidate-icon img{
    height:70px;
    border-radius:50%;
    border:3px solid white;
    z-index: 999;
}
.small-bar{
    margin: -25px 30px;height: 20px;background: gray;display: -webkit-box;display: -ms-flexbox;display: flex;
}
.horizone-line{
    width: 2px;
    height: 20px;
    background: #f4f5fa;
    position: absolute;
    margin-left: 50%;
}

